body {
  font-family: sans-serif;
  margin: 0;
  padding: 0;
  color: #333;
}

.container {
  width: 80%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

nav {
  display: flex;
  gap: 20px;
}
nav a {
  text-decoration: none;
  color: #3498db;
}

main {
  padding: 20px 0;
}

h1 {
  color: #3498db;
}

.my-btn {
  padding: 10px;
  border-radius: 8px;
  background-color: #3498db;
  color: #fff;
  border: none;
  cursor: pointer;
}
.my-btn:hover {
  background-color: rgb(95.3179916318, 173.9790794979, 226.6820083682);
}

.box {
  background-color: #e74c3c;
  padding: 10px;
  margin-top: 10px;
  color: #fff;
}

.show {
  display: block;
}

.hidden {
  display: none;
}

footer {
  text-align: center;
  padding: 20px 0;
  background-color: #f4f4f4;
}